import { Component, OnInit } from '@angular/core';

@Component({
  selector: 'app-easoc',
  templateUrl: './easoc.component.html',
  styleUrls: ['./easoc.component.css']
})
export class EasocComponent implements OnInit {

  images: string[] = [
    'src/assets/easoc/1.png',
    'src/assets/easoc/2.png',
    'src/assets/easoc/3.png',
    'src/assets/easoc/4.png',
    'src/assets/easoc/5.png',
    'src/assets/easoc/6.png'
  ]

  currentImageIndex: number = 0

  constructor() { }

  ngOnInit() {
    this.changeImage()
  }

  changeImage () {
    const numberOfImages: number = this.images.length
    setTimeout( () => {
      if (!this.images[this.currentImageIndex + 1]) {
        this.currentImageIndex = 0
      }
      else {
        this.currentImageIndex++
      }

      this.changeImage()

    }, 15000)
  }

}
