import { BrowserModule } from '@angular/platform-browser';
import { NgModule } from '@angular/core';
import { BrowserAnimationsModule } from '@angular/platform-browser/animations'
import { RouterModule, Routes } from '@angular/router'

import { NgZorroAntdModule, NZ_I18N, en_US } from 'ng-zorro-antd';

import { AppComponent } from './app.component';

import { registerLocaleData } from '@angular/common';
import en from '@angular/common/locales/en';
import { MeComponent } from './components/me/me.component';
import { EasocComponent } from './components/projects/easoc/easoc.component';
import { StudymeComponent } from './components/projects/studyme/studyme.component';
import { HeaderComponent } from './components/header/header.component';
import { FooterComponent } from './components/footer/footer.component';
import { StudentGeneratorComponent } from './components/projects/student-generator/student-generator.component';
registerLocaleData(en);

const appRoutes: Routes = [
  {path: '', component:MeComponent},
  {path: 'easoc', component: EasocComponent},
  {path: 'studyme', component: StudymeComponent},
  {path: 'student-generator', component: StudentGeneratorComponent}
]

@NgModule({
  declarations: [
    AppComponent,
    MeComponent,
    EasocComponent,
    StudymeComponent,
    HeaderComponent,
    FooterComponent,
    StudentGeneratorComponent
  ],
  imports: [
    BrowserModule,
    BrowserAnimationsModule,
    NgZorroAntdModule,
    RouterModule.forRoot(appRoutes)
  ],
  providers: [ { provide: NZ_I18N, useValue: en_US } ],
  bootstrap: [AppComponent],
})
export class AppModule { }
