import { Component, OnInit } from '@angular/core';

@Component({
  selector: 'app-student-generator',
  templateUrl: './student-generator.component.html',
  styleUrls: ['./student-generator.component.css']
})
export class StudentGeneratorComponent implements OnInit {

  images: string[] = [
    'src/assets/student-generator/1.png',
    'src/assets/student-generator/2.png',
    'src/assets/student-generator/3.png',
  ]

  currentImageIndex: number = 0

  constructor() { }

  ngOnInit() {
    this.changeImage()
  }

  changeImage () {
    const numberOfImages: number = this.images.length
    setTimeout( () => {
      if (!this.images[this.currentImageIndex + 1]) {
        this.currentImageIndex = 0
      }
      else {
        this.currentImageIndex++
      }

      this.changeImage()

    }, 15000)
  }

}
